<!-- views -->
<template>
    <section class="entrance">
        <!--  -->
        <header-nav/>
        <!--  -->
        <section class="content">
            <el-row :gutter="20">
                <el-col :span="17">
                    <!--
                        transitionvuetransition
                        transitionenter-active-class
                        leave-active-class
                        mode="out-in"
                        vueanimatedhtml
                        transitionanimated+
                    -->
                    <transition
                            mode="out-in"
                            name="custom-classes-transition"
                            enter-active-class="animated fadeInLeft"
                            leave-active-class="animated bounceOutLeft"
                    >
                        <keep-alive v-show="show">
                            <router-view class="main-content"/>
                        </keep-alive>
                    </transition>
                </el-col>
                <el-col :span="7">
                    <transition
                            mode="out-in"
                            name="custom-classes-transition"
                            enter-active-class="animated fadeInRight"
                            leave-active-class="animated bounceOutRight"
                    >
                        <list-nav v-show="listShow"></list-nav>
                    </transition>
                </el-col>
            </el-row>
        </section>
    </section>
</template>

<script>
    import {Vue, Component} from 'vue-property-decorator'
    import HeaderNav from './other/header'
    import ListNav from "./list"
    import jsCookie from 'js-cookie'
    import {Action} from 'vuex-class'


    @Component({components: {HeaderNav, ListNav}})

    export default class Entrance extends Vue {
        @Action setUserInfo

        show = false
        listShow = false

        async mounted() {
            if (jsCookie.get('email')) {
                const cookie = this.$util.DecodeCookie(jsCookie.get('email'))
                const {data} = await this.$api.getUserInfo({cookie})
                if (data.code === 0) {
                    this.setUserInfo(data.data)
                }
            }
            this.show = true
            this.listShow = true
        }
    }
</script>
<style lang="less" scoped>
    .entrance {
        height: 100%;
        width: 100%;

        .content {
            width: 80%;
            margin: 20px auto;
            /*margin-top: 20px;*/
        }

        .footer {
            width: 100%;
            height: 50px;
            background-color: bisque;
        }
    }
</style>
